import React, {useEffect, useState} from "react";
import { Link } from "gatsby";
import ReactGA from 'react-ga';

// Components
import SEO from "../SEO";
import ArticleCard from "../../components/ArticleCard";

import {createBlogPageMetadata} from "../../jsonLd";

import "./styles.scss";

export default function Template({
  data, tag, title
}) {
  const [start] = useState(new Date());
  useEffect(() => {
    return () => {
      const cur = new Date();
      const diff = cur.getMilliseconds() - start.getMilliseconds();
      ReactGA.timing({
        category: 'TimeOnBlogList',
        variable: 'time',
        value: diff, // in milliseconds
        label: tag,
      });
    };
  });

  const metadata = {
    ...createBlogPageMetadata(data.edges),
    name: `TechWaifu's Blog Posts Related to #${tag}`,
  };
  const posts = data.edges;
  const prevPageLink = data.pageInfo.currentPage === 2 ? `/${tag}` : `/${tag}/${data.pageInfo.currentPage - 1}`;
  const curPageLink = data.pageInfo.currentPage === 1 ? `${tag}` : `${tag}/${data.pageInfo.currentPage }`;
  return (
    <section className="BlogListPage">
      <SEO
        title={`Recent blog posts for #${title}`}
        keywords={[title]}
        urlPath={curPageLink}
        metadata={JSON.stringify(metadata)}
      />
      <h1 className="Title">{title}</h1>
      {posts.map(({ node }) => {
        return (
          <ArticleCard key={node.id} data={node} />
        );
      })}
      <div className="PaginationButtonCtr">
        {data.pageInfo.hasPreviousPage &&
          <Link className="PaginationButton" to={prevPageLink}>Prev</Link>
        }
        {data.pageInfo.hasNextPage &&
          <Link className="PaginationButton Next" to={`/${tag}/${data.pageInfo.currentPage + 1}`}>Next</Link>
        }
      </div>
    </section>
  );
}
